.paginatorContainer {
    margin-bottom: 3vh;
    margin-left: 2vw;
    text-align: center;
}

.paginatorContainer button {
    text-decoration: underline;
    background: none;
    border: none;
    cursor: pointer;
    color: rgba(38, 40, 41, 0.9);
    font-size: 1rem;
    font-weight: 100;
    margin: 0 0.5vw;
}


.page.selectedPage {
    font-weight: bold;
    color: rgba(185, 215, 237, 0.5);
}
